<template>
<div>
    <loader :is-visible="isLoading"></loader>
    <section class="main-content">
        <div ref="hellp" id="divMaindealData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('AddPolicy') }}</span>
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')" @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                    <dynamic-form ref="AddEmployee" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
                        <template v-slot:tgslot-Probation_Period="{ data }">
                            <div class="col-lg-12 mt-3">
                    <div class="heading-border mb-2">
                        <h5 class="h5">
                            <span>{{ $t('ProbationPeriod') }}</span><small class="float-right">
                                <a href="javascript:;" id="ProbationPeriod" @click="addNewProbation"><em class="fa fa-plus pr-2"></em>{{ $t('AddNew') }}</a>
                            </small>
                        </h5>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="listing">
                        <div class="table-responsive">
                            <table id="tblProbationPeriod" class="table table-bordered dataTable no-footer">
                                <thead class="thead-bg">
                                    <tr>
                                        <th min-width="150px !important;"><span class="reqfield">{{$t('Department')}}</span></th>
                                        <th min-width="150px !important;"><span class="reqfield">{{ $t('Duration') }}</span></th>
                                        <th min-width="150px !important;"><span>{{ $t('Days') }}</span></th>
                                        <th class="text-center"><span>{{ $t('Action') }}</span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(probation, index) in probations" :key="index">
                                        <td>
                                            <Field :name="'Department'" v-slot="{ errors }" :rules="{ 'required': true }">
                                            <select :class="{ 'form-control valid': true, 'is-invalid': errors[0] }" v-model="probation.department_id" @change="probationDepartmentChange(probation)">
                                                <option value="">{{ $t('Select') }}</option>
                                                <option v-for="(option, optionIndex) in DepartmentData" v-bind:value="String(option.value)" :key="optionIndex"> {{ $t(option.name) }}</option>
                                            </select>
                                            <span v-if="errors[0]" :name="'Department'" class="invalid-feedback d-block">{{errors[0] }}</span>
                                          </Field>
                                        </td>
                                        <td>
                                            <div class="input-group">
                                                <Field :name="'Duration'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                <input :class="{ 'form-control txtFirstValue': true, 'is-invalid': errors[0] }" v-model="probation.duration" @input="checkLength(probation)" type="text" autocomplete="off" @keypress="check($event)">
                                                <span v-if="errors[0]" :name="'Duration'" class="invalid-feedback d-block">{{errors[0] }}</span>
                                                </Field>
                                                <span class="input-group-text p-0 border-0 bg-white">
                                                    <select v-model="probation.duration_type" class="nosearch form-control">
                                                        <option value="1">{{ $t('Month/s') }}</option>
                                                        <option value="2">{{ $t('Day/s') }}</option>
                                                    </select>
                                                </span>
                                            </div>
                                            <span class="field-validation-valid" style="display:none; font-weight: normal; font-size: 90%;" id="messageProbationDuration">
                                                <!-- Validation message if needed -->
                                            </span>
                                        </td>
                                        <td class="align-top">
                                            <select class="form-control" v-model="probation.days_type">
                                                <option value="1">{{ $t('Calendar') }}</option>
                                                <option value="2">{{ $t('Business') }}</option>
                                            </select>
                                        </td>
                                        <td class="single-action text-center">
                                            <a class='delete-row' title='Delete' @click="deleteProbation(index)" v-show="index > 0">
                                                <em class='fa fa-trash text-danger'></em>
                                            </a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                        </template>
                        <template v-slot:tgslot-Notice_period="{ data }">
                            <div class="col-lg-12 mt-3">
                    <div class="heading-border mt-3 mb-2">
                        <h5 class="h5">
                            <span>{{ $t('NoticePeriod') }}</span><small class="float-right">
                                <a href="javascript:;" id="NoticePeriod" @click="addNewNotice"><em class="fa fa-plus pr-2"></em>{{ $t('AddNew') }}</a>
                            </small>
                        </h5>
                    </div>
                </div>
                <div class="col-lg-12">
                    <div class="listing">
                        <div class="table-responsive">
                            <table id="tblNoticePeriod" class="table table-bordered dataTable no-footer">
                                <thead class="thead-bg">
                                    <tr>
                                        <th min-width="150px !important;"><span class="reqfield">{{$t('Department')}}</span></th>
                                        <th min-width="150px !important;"><span class="reqfield">{{ $t('Duration') }}</span></th>
                                        <th min-width="150px !important;"><span>{{ $t('Days') }}</span></th>
                                        <th class="text-center"><span>{{ $t('Action') }}</span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(notice, index) in notices" :key="index">
                                        <td>
                                            <Field :name="'department'" v-slot="{ errors }" :rules="{ 'required': true }">
                                            <select :class="{ 'form-control valid': true, 'is-invalid': errors[0] }" v-model="notice.department_id" @change="noticeDepartmentChange(notice)">
                                                <option value="">{{ $t('Select') }}</option>
                                                <option v-for="(option, optionIndex) in DepartmentData"
                                                                    v-bind:value="String(option.value)"
                                                                    :key="optionIndex">
                                                                    {{ $t(option.name) }}</option>
                                            </select>
                                              <span v-if="errors[0]" :name="'department'" class="invalid-feedback d-block">{{errors[0] }}</span>
                                          </Field>
                                        </td>
                                        <td>
                                            <div class="input-group">
                                                <Field :name="'duration'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                <input :class="{ 'form-control txtFirstValue': true, 'is-invalid': errors[0] }" v-model="notice.duration" @input="checkLength(notice)" @keypress="check($event)" type="text" autocomplete="off">
                                                <span v-if="errors[0]" :name="'duration'" class="invalid-feedback d-block">{{errors[0] }}</span>
                                                </Field>
                                                <span class="input-group-text p-0 border-0 bg-white">
                                                    <select v-model="notice.duration_type" class="nosearch form-control">
                                                        <option value="1">{{ $t('Month/s') }}</option>
                                                        <option value="2">{{ $t('Day/s') }}</option>
                                                    </select>
                                                </span>
                                            </div>
                                            <span class="field-validation-valid" style="display:none; font-weight: normal; font-size: 90%;" id="messageNoticeDuration">
                                                <!-- Validation message if needed -->
                                            </span>
                                        </td>
                                        <td class="align-top">
                                            <select class="form-control" v-model="notice.days_type">
                                                <option value="1">{{ $t('Calendar') }}</option>
                                                <option value="2">{{ $t('Business') }}</option>
                                            </select>
                                        </td>
                                        <td class="single-action text-center">
                                            <a class='delete-row' title='Delete' @click="deleteNotice(index)" v-show="index > 0">
                                                <em class='fa fa-trash text-danger'></em>
                                            </a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                        </template>
                    </dynamic-form>
            </div>
        </div>
    </section>
</div>
</template>

<script>
import DataService from '../../../services/DataService'
export default {
    data() {
        return {
            probations: [{
                department_id: '',
                duration: '',
                duration_type: '1',
                days_type: '1'
            }],
            notices: [{
                department_id: '',
                duration: '',
                duration_type: '1',
                days_type: '1'
            }],
            isLoading: false,
            DepartmentData:[],
            CompanyId:null,
            Userid:null,
            policyModels:[],
            MandatoryString: 'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('PolicyName'),
                            name: "PolicyName",
                            value: "",
                            placeholder: "Policy Name",
                            validationRules: {
                                "required": true,
                                max: 100
                            }
                        },
                        {
                            astype: "DateField",
                            label: this.$t('StartDate'),
                            name: "StartDate",
                            value: "",
                            placeholder: "Enter Start Date",
                            config: {
                                options: [ ],
                                minDate: new Date() 
                            },
                            validationRules: {
                                "required": true
                            },
                            visibility: true,
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        validationRules: "",
                    }]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "Probation_Period",
                            value: "",
                            visibility: true,
                            validationRules: { "required": false },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "Notice_period",
                            value: "",
                            visibility: true,
                            validationRules: { "required": false },
                        },
                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                }
            ]
        }
    },
    created:async function () {
        var vm = this;
        vm.CompanyId= this.GetUserInfo.CompanyId;
        vm.Userid= this.GetUserInfo.ID;
        vm.PolicyId = this.$route.params.id;
        await vm.ShowDepartment();
        if(vm.PolicyId>0){
            await vm.GetPolicyDataById();
        }
    },
    methods: {
        Close: function () {
            this.$router.push({
                name: 'PolicyListing'
            });
        },
        GetPolicyDataById: async function () {
            var vm = this;
            var params = `id=${vm.PolicyId}`
            await DataService.GetPolicyDataById(params).then(function (response) {
                if(response.data){
                    vm.FormSchema[0].Data[0].value=response.data[0].name;
                    vm.FormSchema[0].Data[1].value=response.data[0].start_date;
                    vm.FormSchema[1].Data[0].value=response.data[0].description;
                    var count=0;
                    response.data.forEach((item, index) => {
                        if (index == 0) {
                            vm.probations.splice(index, 1);
                            vm.notices.splice(index, 1);
                        }
                        if(item.period_type==false)  { 
                        vm.probations.push({
                            department_id: '',
                            duration: '',
                            duration_type: '1',
                            days_type: '1'
                        });
                        vm.probations[index].department_id = item.department_id;
                        vm.probations[index].duration = item.duration;
                        vm.probations[index].duration_type = item.duration_type_id;
                        vm.probations[index].days_type = item.days_type_id;
                      }else{
                        vm.notices.push({
                            department_id: '',
                            duration: '',
                            duration_type: '1',
                            days_type: '1'
                        });
                        vm.notices[count].department_id = item.department_id;
                        vm.notices[count].duration = item.duration;
                        vm.notices[count].duration_type = item.duration_type_id;
                        vm.notices[count].days_type = item.days_type_id;
                        count++;
                    }
                });
                }
            })
        },
        ShowDepartment: async function () {
            var vm = this;
            vm.DepartmentData=[];
            var params = `ids=&moduleName=&type=DEPARTMENT_TYPE&search=&refId=`
            await DataService.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[0].Data[1].config.options = response.data.DATA;
                    vm.DepartmentData= response.data.DATA;
                }
                vm.$refs.AddEmployee.UpdateKeyValue();
            })
        },
        addNewProbation() {
            this.probations.push({
                department_id: '',
                duration: '',
                duration_type: '1',
                days_type: '1'
            });
        },
        addNewNotice() {
            this.notices.push({
                department_id: '',
                duration: '',
                duration_type: '1',
                days_type: '1'
            });
        },
        deleteProbation(index) {
            if (index > 0) {
                this.probations.splice(index, 1);
            }
        },
        deleteNotice(index) {
            if (index > 0) {
                this.notices.splice(index, 1);
            }
        },
        probationDepartmentChange(probation) {
            // Handle department change if needed
        },
        noticeDepartmentChange(notice) {
            // Handle department change if needed
        },
        checkLength(item) {
        var vm=this;
        const id = `#${this.inputId}`;
        const fieldValue = item.duration;
        const fieldLength = fieldValue.length;
        if (fieldLength > 3) {
            item.duration = fieldValue.substring(0, 3);
        } 
        },
        
         check:function(event){
            const keyCode = event.keyCode;
            if (keyCode < 48 || keyCode > 57) {
            event.preventDefault();
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            var TypeValue="";
            if(vm.PolicyId>0){
                TypeValue="UPDATE";
            }else{
                TypeValue= "INSERT";
            }
            var PolicyName=formfields.PolicyName;
            var Policydesc=formfields.Description;
            var PolicyDate=formfields.StartDate;
            vm.probations.forEach(item => {
                    var ListTargetObj = {
                        policy_id:vm.PolicyId,
                        name: PolicyName,
                        description: Policydesc,
                        start_date: PolicyDate,
                        status: 1001,
                        company_id: vm.CompanyId,
                        user_id: vm.Userid,
                        department_id: item.department_id,
                        duration_type_id: item.duration_type,
                        duration: item.duration,
                        days_type_id: item.days_type,
                        period_type: 0,
                        TYPE_VAL: TypeValue,
                        DurationType: item.duration_type,
                        DaysType: item.days_type
                    }
                    vm.policyModels.push(ListTargetObj);
             })

             vm.notices.forEach(item => {
                    var ListnoticeObj = {
                        policy_id:vm.PolicyId,
                        name: PolicyName,
                        description: Policydesc,
                        start_date:PolicyDate,
                        status: 1001,
                        company_id: vm.CompanyId,
                        user_id: vm.Userid,
                        department_id: item.department_id,
                        duration_type_id: item.duration_type,
                        duration: item.duration,
                        days_type_id: item.days_type,
                        period_type: 1,
                        TYPE_VAL: "INSERT",
                        DurationType: item.duration_type,
                        DaysType: item.days_type
                    }
                    vm.policyModels.push(ListnoticeObj);
             })
            var jsonobj = vm.policyModels;
            var params = JSON.stringify(jsonobj);
            DataService.AddEditPolicy(params).then((response) => {
                if (response.data[0].result== 1) {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Policy")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }else if(response.data[0].result== -5){
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Policy")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Policy")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>
